"use client";
import React, { useState } from "react";

export default function Canvas() {
  const [droppedComponents, setDroppedComponents] = useState<any[]>([]);

  const onDragOver = (e: React.DragEvent) => {
    e.preventDefault();
  };

  const onDrop = (e: React.DragEvent) => {
    e.preventDefault();
    const componentData = JSON.parse(e.dataTransfer.getData("application/json"));
    setDroppedComponents([...droppedComponents, componentData]);
  };

  const renderComponent = (component: any) => {
    switch (component.type) {
      case "text_input":
        return (
          <div className="mb-4">
            <label className="block text-sm font-medium text-gray-700 dark:text-gray-300">
              {component.properties.label}
            </label>
            <input
              type="text"
              placeholder={component.properties.placeholder}
              className="w-full bg-gray-50 dark:bg-gray-700 border border-gray-300 dark:border-gray-600 rounded-md p-2 text-sm"
            />
          </div>
        );
      // Add cases for other component types here
      default:
        return (
          <div className="p-4 bg-gray-200 dark:bg-gray-700 rounded-md mb-2">
            <p className="font-semibold">{component.label}</p>
            <p className="text-sm text-gray-500 dark:text-gray-400">
              {component.description}
            </p>
          </div>
        );
    }
  };

  return (
    <div
      onDragOver={onDragOver}
      onDrop={onDrop}
      className="flex-1 bg-gray-100 dark:bg-gray-800 p-8"
    >
      <div className="bg-white dark:bg-gray-900 rounded-lg shadow-md p-6 min-h-full border-2 border-dashed border-gray-300 dark:border-gray-700">
        {droppedComponents.length > 0 ? (
          droppedComponents.map((component, index) => (
            <div key={index}>{renderComponent(component)}</div>
          ))
        ) : (
          <div className="flex items-center justify-center h-full">
            <div className="text-center text-gray-500 dark:text-gray-400">
              <p className="text-lg font-semibold">Drop components here</p>
              <p className="text-sm">
                Drag and drop from the left panel to build your form.
              </p>
            </div>
          </div>
        )}
      </div>
    </div>
  );
}
